export const getOrderInfo = (id) => {
  return fetch(`${BASE_URL}${ORDERS}/${id}`, {
    // id 402 установлен для пропуска этапа авторизации
    method: "GET",
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      // "Access-Control-Allow-Origin": `${BASE_URL}`,
    },
  }).then(getResponse);
};

