export const getOrdersInfo = () => {
  return fetch(`${BASE_URL}${ORDERS_SEARCH}`, {
    method: "GET",
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
    },
  }).then(getResponse);
};

// https://g123124.bounceme.net/api/sorted_orders?from_town=Port Patrick&to_town=Parkerbury&to_datetime=1978-12-29T01:46:58.0000002

export const getSearchedOrdersInfo = (e) => {
  return fetch(`${BASE_URL}${SORTED_ORDERS}`, {
    method: "POST",
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      from_town: e.from,
      to_town: e.to,
      from_datetime: e.date,
    }),
  }).then(getResponse);
};

export const signup = (e) => {
  return fetch(`${BASE_URL}${REGISTER}`, {
    method: "POST",
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      // login: "testuser11111",
      first_name: e.name,
      middle_name: e.second_name,
      last_name: e.surname,
      phone: `+351 ${e.phone}`,
      email: e.email,
      password: e.password,
      confirm_password: e.confirm_password,
      role: "sender"
    }),
  }).then(getResponse);
};

